*{
    box-sizing: border-box;
}
body{
    font-family: sans-serif;
    margin: 0;
}
main{
    padding: 36px;
}
.header{
    display: flex;
    align-items: center;
    height: 65px;
    background:linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
    color: white;
    padding: 20px;
}
.header_image{
    height: 100%;
    margin-right: 6px;
}
.header_title{
    font-size: 1.2rem;
    margin-right: auto;
}
.header_project{
  font-size: 20px;
  font-weight: 500;  
}
.form_meme{
    display: grid;
    grid-template:40px 40px/ 1fr 1fr ;
    gap: 17px;
    
}
.Input_box{
    font-family: sans-serif;
    border-radius: 5px;
    border: 1px solid #D5D4D8;
    text-indent: 5px;

}
.form_button{
    grid-column: 1/-1;
    font-family: sans-serif;
    border-radius: 5px;
    background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
    color: white;
    border: none;
    cursor: pointer;
}
.meme{
    position: relative;
    margin-top: 40px;
}
.meme_image{
    max-width: 100%;
    border-radius: 3px;
}
.meme_text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.bottom {
    bottom: 0;
}

.top {
    top: 0;
}